define('reciprocity/router', ['exports', 'ember', 'reciprocity/config/environment'], function (exports, Ember, config) {

  'use strict';

  var Router = Ember['default'].Router.extend({
    location: config['default'].locationType
  });

  Router.map(function () {
    this.route("faq");
    this.resource("step", function () {
      this.route("username");
      this.route("repositories");
      this.route("results");
    });
  });

  exports['default'] = Router;

});